import Sidebar from "@components/sidebar";
import { isObjEmpty } from "@utils";
import classnames from "classnames";
import { Editor } from "react-draft-wysiwyg";
import { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import "react-phone-number-input/style.css";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label, Media, Row } from "reactstrap";
import validateOptions from "../../../../constants/validate";
import { addCategory } from "../store/action";
import { convertToRaw, EditorState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import Select from "react-select";
import { getData, getKind } from "../store/action";

const SidebarNewCategory = ({
  open,
  toggleSidebar,
  intl,
  disable,
  setDisable,
}) => {
  const store = useSelector((state) => state.category);
  const [changeSelect, setChangeSelect] = useState();
  const { data } = useSelector((state) => state.kind);
  const lang = useSelector((state) => state.common.language);
  const CategoryOptions = validateOptions.CategoryOptions;
  const dispatch = useDispatch();
  const cate = yup.object({
    // categoryId: yup
    //   .number()
    //   .required(<FormattedMessage id="The category field is required" />),
    name: yup
      .string()
      .required(<FormattedMessage id="The category name field is required" />)
      .min(
        3,
        <FormattedMessage id="Category name must be at least 3 characters" />
      )
      .max(70, <FormattedMessage id="Category must be 70 characters max" />),
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    getValues,
    setValue,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  const onSubmit = async (values) => {
    const category_description =
      (valueDescription &&
        draftToHtml(convertToRaw(valueDescription?.getCurrentContent()))) ||
      null;

    try {
      dispatch(
        addCategory({
          ...values,
          name: values.name || undefined,
          description: category_description || undefined,
          lang,
        })
      );
    } catch (error) {
      console.log(error);
    }
  };

  const [valueDescription, setValueDescription] = useState(
    EditorState.createEmpty()
  );
  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="Add New Post Category" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label for="name">
            <FormattedMessage id="catename" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="name"
            id="name"
            placeholder=""
            innerRef={register(CategoryOptions.name)}
            onBlur={() => {
              let name = document.getElementById("name");
              if (name && name.value) {
                name.value = name.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["name"] })}
          />
          <small className="text-danger">
            {errors?.name && errors.name.message}
          </small>
          {errors?.name?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="description">
            <FormattedMessage id="Description" />{" "}
            <span className="text-danger"></span>
          </Label>

          <Editor
            stripPastedStyles={true}
            toolbar={{
              options: ["inline", "textAlign"],
              inline: {
                inDropdown: false,
                options: ["bold", "italic", "underline"],
              },
            }}
            editorState={valueDescription}
            onEditorStateChange={(data) => setValueDescription(data)}
            name="description"
            innerRef={register(CategoryOptions.description)}
          />
          <small className="text-danger">
            {errors?.description && errors.description.message}
          </small>
        </FormGroup>

        {/* <FormGroup>
          <Label>
            <FormattedMessage id="Solution" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="categoryId"
            render={({ field }) => {
              return (
                <Select
                  id="categoryId"
                  innerRef={register}
                  name="categoryId"
                  placeholder={<FormattedMessage id="Select_solution" />}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["categoryId"],
                        }
                      : ""
                  )}
                  options={data?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.name,
                      number: index + 1,
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    console.log(e);
                    setError("categoryId", "");
                    setValue("categoryId", e?.value);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.categoryId && errors.categoryId.message}
          </small>
        </FormGroup> */}

        <FormGroup>
          <Label for="status">
            <FormattedMessage id="status" />{" "}
          </Label>

          <Input
            type="select"
            name="status"
            id="status"
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Active" })}</option>
            {/* <option value="0">{intl.formatMessage({ id: "Deactive" })}</option> */}

            <option value="2">{intl.formatMessage({ id: "Blocked" })}</option>
          </Input>
        </FormGroup>

        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="add" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarNewCategory);
